import {
    mapState,
    mapMutations
} from 'vuex'
export default {
	data() {
		return {
			source: 0,
			addressList: []
		}
	},
	methods: {
		changeDefaultAddress(addressId){
			this.$http.post('/mall/app/address/default', {
				...this.baseInfo,
				accountId: this.userInfo.accountId,
				addressId: addressId,
				addressSelected: 1
			})
			.then( res => {
				console.log(res)
				if(res.code == 0){
					this.initAddressData();
				}
			})
			.catch( err => {
				console.log(err);
			})
		},
		//选择地址
		checkAddress(item, index){
			// const prePage = ()=>{
			// 	let pages = getCurrentPages();
			// 	let prePage = pages[pages.length - 2];
			//


			// 	return prePage.$vm;
			// }
			if(this.source == 1){
				// prePage().address = item;
				// console.log(prePage().address)
				uni.setStorageSync('chooseAddress', item);
				// this.addressList.map((v, i) => {
				// 	v.default=false;
				// })
				// this.addressList[index].default = true;
				uni.navigateBack();
			}
		},
		addAddress(type, item){
			uni.navigateTo({
				url: `/pages/addressManage/addressManage?type=${type}&data=${item}`
			})
		},
		//添加或修改成功之后回调
		// refreshList(data, type){
		// 	//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
		// 	this.addressList.unshift(data);
		// 	
		// 	console.log(data, type);
		// },
		initAddressData(){
			this.$http.post('/mall/app/address/list', {
				...this.baseInfo,
				accountId: this.userInfo.accountId
			})
			.then( res => {
				if(res.code == 0){
					this.addressList=res.result;
				}
			})
			.catch( err => {
				console.log(err);
			})
		}
	},
	computed: {
		...mapState('common', ['baseInfo', 'userInfo'])
	},
	onLoad(option){
		// console.log(option.source);
		this.source = option.source;
		
	},
	onShow(){
		this.initAddressData();
	}
}
